.floatLabel
{
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
}

.floatLabel input {
    height: 32px;
    padding: 10px 16px 0 6px;
    outline: 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    background: #fff;
    font-family: 'Gmarket Sans TTF Medium',serif;
    font-size: 14px;
}

.floatLabel label {
    font-size: 14px;
    padding: 0 10px;
    color: #999;
    pointer-events: none;
    position: absolute;
    transform: translate(0, 80%) scale(1);
    transform-origin: top left;
    transition: all 0.19s ease-out;
}

.floatLabel:focus-within label {
    transform: translate(0, 5px) scale(0.7);
}

.floatLabel .Active {
    transform: translate(0, 5px) scale(0.7);
}
